import React, { Component } from "react";
import "./signUpPage.scss";
import { Link } from "react-router-dom";
import LoginPageText from "../../img/Login/logo-hipotrofia 1.png";
import Axios from "axios";

class SignUpPage extends Component{
    constructor(){
        super();
        this.state={
        email:"",
        errorEmail:"",
        password:"",
        errorPassword:"",
        error:null,
        isInvalid:false,
        secondPassword:""
        };
    }

    onClick = () => {
        const user= {
          name: "",
          email: this.state.email,
          password:this.state.password,
          secondPassword:this.state.secondPassword,
        };
    
        

        Axios.post(`http://localhost:3001/users`, {user})
        .then(response => {
          console.log(response);
          console.log(response.data)
        })
      };
      onChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
      };
      validateEmail = () => {
        const { email } = this.state;
        let isEmailValid = true;
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!re.test(email)) {
          isEmailValid = false;
          this.setState({ errorEmail: "Podany email jest nieprawid≈Çowy!" });
        } else {
          this.setState({ errorEmail: "" });
          isEmailValid = true;
        }
        return isEmailValid;
      };
      validatePassword = () => {
        const { password, secondPassword } = this.state;
        let isPasswordValid = true;
    
        if (password === "" || password.length <= 6 || password === secondPassword) {
          isPasswordValid = false;
          this.setState({
            errorPassword: "Has≈Ço jest b≈Çƒôdne",
          });
        } else {
          this.setState({ errorPassword: "" });
          isPasswordValid = true;
        }
        return isPasswordValid;
      };


    
    render(){

        const { email, password, errorEmail, errorPassword, error } = this.state;

        const isInvalid = password === "" || email === "";

        return(
            <div className="container signup-page">
        <div className="signup-form-container">
          <div className="signup-form-container-form">
            
              <h1>Witamy...</h1>

            
              <div className="signup-box">
                <form onSubmit={this.onSubmit}>

                <div className="name-container">
                    <label>Tw√≥j Nick</label>
                    <input
                      id="name"
                      name="name"
                      // value={email}
                      onChange={this.onChange}
                      // onBlur={this.validateEmail}
                      type="text"
                      placeholder="Tw√≥j Nick"
                    />
                  </div>

                  <div className="email-container">
                    <label>Email</label>
                    <input
                      id="email"
                      name="email"
                      // value={email}
                      onChange={this.onChange}
                      onBlur={this.validateEmail}
                      type="text"
                      placeholder="imiƒô@email.com"
                    />
                  </div>
                  {errorEmail && <span className="errorMessage">{errorEmail}</span>}

                  <div className="password-container">
                    <label>Has≈Ço</label>
                    <input
                      id="password"
                      name="password"
                      // value={password}
                      onChange={this.onChange}
                      onBlur={this.validatePassword}
                      type="password"
                      placeholder="Has≈Ço"
                    />
                  </div>
                  <div className="password-container">
                    <label>Powt√≥rz Has≈Ço</label>
                    <input
                      id="secondPassword"
                      name="secondPassword"
                      // value={password}
                      onChange={this.onChange}
                    //   onBlur={this.validatePassword}
                      type="password"
                      placeholder="powt√≥rz Has≈Ço"
                    />
                  </div>
                  {/* {errorPassword && (
                        <span className="errorMessage">{errorPassword}</span>
                      )} */}
                </form>
              </div>

            <div className="signup-page-buttons-container">
              
                <>
                  <button
                    className="signup-button"
                    type="submit"
                    onClick={this.onClick}
                  >
                    Zarejestruj
                  </button>

                  <p className="login-button">
                    <Link to={"/login"}>Zaloguj</Link>
                  </p>

                </>
             
           
            </div>
            {/* {error && <p className="errorMessage">{error.message}</p>} */}
          </div>
        </div>
        <div className="loginpage-photo-container">
          <div className="background-image">
            <img
              className="text-image"
              src={LoginPageText}
              alt="login page text hipotrofia"
            ></img>
          </div>
        </div>
      </div>
        )
    }
}

export default SignUpPage;

